<template>
    <svg class="svg-icon" aria-hidden="true">
        <use :xlink:href="name"></use>
    </svg>
</template>

<script>
    export default {
        name: 'self-icon',
        props: {
            type: {
                type: String,
                required: true
            }
        },
        computed: {
            name() {
                return `#icon-${this.type}`
            }
        }
    }
</script>

<style lang="scss" scoped>
    .svg-icon {
        width: 1em;
        height: 1em;
        vertical-align: -0.15em;
        fill: currentColor;
        overflow: hidden;
    }
</style>