<template>
	<div class="publicBox">
		<publicTitle title="" />
		<div class="Statistics">
			<swiper ref="mySwiper" :options="swiperOptions">
				<!-- <swiper-slide class="StatisticsContent">
					<div>
						<img :src="require('@/assets/circle1.png')" />
						<span>{{MainPoolIncrease}}</span>
					</div>
					<span>/ETH</span>
				</swiper-slide> -->
				<swiper-slide class="StatisticsContent">
					<div>
						<img :src="require('@/assets/circle2.png')" />
						<span>{{totalDepositAmount}}</span>
					</div>
					<span>/ETH</span>
				</swiper-slide>
				<swiper-slide class="StatisticsContent">
					<div>
						<img :src="require('@/assets/circle3.png')" />
						<span>{{AssertPoolEther}}</span>
					</div>
					<span>/ETH</span>
				</swiper-slide>
				<swiper-slide class="StatisticsContent">
					<div>
						<img :src="require('@/assets/circle4.png')" />
						<span>{{LuckPoolIncrease}}</span>
					</div>
					<span>/ETH</span>
				</swiper-slide>
			</swiper>
		</div>
	</div>
</template>

<script>
	import publicTitle from '@/components/publicTitle.vue'
	export default {
		components:{
			publicTitle
		},
		data(){
			return {
				swiperOptions: {
					slidesPerView: 3,
					spaceBetween: 1,
					centeredSlides: true,
					loop: true,
				},
				LuckPoolIncrease: 0,
				totalDepositAmount: 0,
				MainPoolIncrease: 0,
				AssertPoolEther: 0,
			}
		},
		mounted() {
			
			//
			this.$web3.eth.getBalance(this.$contract.getI('Main')._address).then(async res=>{
				let MainPoolIncrease = (await this.$contract.get('Env').methods.getUint(14).call()).fromWei()
				this.MainPoolIncrease = (Number(res.fromWei()) + Number(MainPoolIncrease)).toFixed(2)
				this.$emit('MainPool',res.fromWei())
			})
			
			//
			this.$web3.eth.getBalance(this.$contract.getI('SmallLuckPool')._address).then(async res=>{
				let LuckPoolIncrease = (await this.$contract.get('Env').methods.getUint(12).call()).fromWei()
				this.LuckPoolIncrease = (Number(res.fromWei()) + Number(LuckPoolIncrease)).toFixed(2)
			})
			
			//
			this.$contract.get('Main').methods.totalDepositAmount().call(async (err,res)=>{
				let totalDepositIncrease = (await this.$contract.get('Env').methods.getUint(11).call()).fromWei()
				this.totalDepositAmount = (Number(res.fromWei()) + Number(totalDepositIncrease)).toFixed(2)
			})
			
			//
			this.$web3.eth.getBalance(this.$contract.getI('AssertPoolEther')._address).then(async res=>{
				this.AssertPoolEther = res.fromWei().toFixed(2)
			})
		}
	}
</script>

<style scoped lang="scss">
	.swiper-slide {
		text-align: center;
		height: auto;
		
		display: flex;
		flex-direction: column;
		justify-content: center;
		
		align-items: center;
		-webkit-transition: 300ms;
		-moz-transition: 300ms;
		-o-transform: 300ms;
		
		transform:scale(0.7);
		-ms-transform:scale(0.7); 
		-moz-transform:scale(0.7); 
		-webkit-transform:scale(0.7);
		-o-transform:scale(0.7); 
	
	}
	
	.swiper-slide-active,.swiper-slide-duplicate-active{
		transform:scale(1);
		-ms-transform:scale(1); 
		-moz-transform:scale(1); 
		-webkit-transform:scale(1); 
		-o-transform:scale(1);
	}
	
	.Statistics{
		margin-top: 40px;
		
		.StatisticsContent{
			display: flex;
			flex-direction: column;
			
			&>div{
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				margin-bottom: 18px;
				
				&>img{
					width: 220px;
				}
	
				&>span{
					position: absolute;
					font-size: 40px;
					left: 50%;
					top: 50%;
					transform: translate(-50%,-50%);
				}
			}
			
			&>span{
				font-size: 24px;
				color: #E4E4E4;
			}
		}
	}
</style>
