<template>
	<div class="box">
		<div class="start">
			<img :src="require('@/assets/startLogo.png')" class="startLogo" />
			<div class="startTitle">
				<p ></p>
				<span>    100%</span>
			</div>
			<div class="startButton">
				<button @click="scrollId('content')">
					<img :src="require('@/assets/playButton.png')" />
					<span></span>
				</button>
				<button>
					<img :src="require('@/assets/videoButton.png')" />
					<span></span>
				</button>
			</div>
			<div class="publicBox">
				<publicTitle title="" />
				<div class="gameIntroduction">Smart Player</div>
			</div>
		</div>
		<div class="content" id="content">
			<div class="publicBox">				
				<publicTitle title="" />
				<div class="playGame">
					<div class="playGameNav">
						<div v-for="(item,index) in playGameNav" :key="index" :class="{active:active == index}" @click="navSwitch(index)">{{item.title}}</div>
					</div>
					<div class="navContent" :class="{activeBorder1: active == 0,activeBorder2: active == 1,activeBorder3: active == 2}">
						<div class="play" v-show="active == 0">
							<div v-show="playShow == 1">
								<div class="playInfo">
									<div>
										<div>
											<img :src="require('@/assets/playInfo1.png')" />
											<span></span>
										</div>
										<span>{{playConfig.minLimit}}{{playConfig.maxLimit}} ETH</span>
									</div>
									<div>
										<div>
											<img :src="require('@/assets/playInfo2.png')" />
											<span></span>
										</div>
										<span>{{playInfo.currentAmount}} ETH</span>
									</div>
									<div>
										<div>
											<img :src="require('@/assets/playInfo3.png')" />
											<span></span>
										</div>
										<span>{{playInfo.currentAmount}} ETH</span>
									</div>
								</div>
								<div class="walletInfo">
									<div class="yellowBlock"></div>
									<div>
										<img :src="require('@/assets/ethLogo.png')" />
										<span>{{ `${$root.address.slice(0, 7)}...${$root.address.slice(-7)}` }}</span>
									</div>
									<div><span>{{banlance}}</span> ETH</div>
								</div>
								<div class="playInput">
									<input type="number" placeholder="" v-model="playAmount" oninput = "value=value.replace(/[^\d]/g,'')" />
									<span>ETH</span>
								</div>
								<div class="fastPlay">
									<div class="yellowBlock"></div>
									<span></span>
									<div><span v-for="(item,index) in fastPlay" :key="index" :class="{active: fastPlayActive === index}" @click="fastPlayActive = index;playAmount = item.value">{{item.value}} ETH</span></div>
								</div>
								<button class="publicButton" @click="playDef"></button>
								<div class="playCheck" @click.shop="ruleOverlay = true">
									<checkbox v-model="rule" checked-color="#DDA33F" @click="rule = false" />
									<span></span>
								</div>
							</div>
							<div v-show="playShow == 2">
								<div class="cureent">
									<div>{{playInfo.currentAmount}}</div>
									<span>/ETH</span>
								</div>
								<div class="ul">
									<div class="list">
										<div>
											<img :src="require('@/assets/currentPlay1.png')" />
											<span></span>
										</div>
										<div><span>{{playInfo.currentDay}}</span> </div>
									</div>
									<div class="list">
										<div>
											<img :src="require('@/assets/currentPlay2.png')" />
											<span></span>
										</div>
										<div>{{playInfo.startTime}}</div>
									</div>
									<div class="list">
										<div>
											<img :src="require('@/assets/currentPlay3.png')" />
											<span></span>
										</div>
										<div :class="{active : playInfo.expired}">{{playInfo.expired ? '' : playInfo.endTime}}</div>
									</div>
									<div class="list" @click="playShow = 3">
										<div>
											<img :src="require('@/assets/currentPlay4.png')" />
											<span></span>
										</div>
										<div><span></span></div>
									</div>
								</div>
								<div class="button">
									<button class="publicButton" @click="playShow = 1"></button>
									<button class="publicButton" v-if="playInfo.expired" @click="divestment"></button>
								</div>
							</div>
							<div v-show="playShow == 3">
								<div class="recordTitle" @click="playShow = 2">
									<span></span>
									<icon name="cross"></icon>
								</div>
								<div class="playRecord">
									<div><span></span><span></span></div>
									<div v-for="(item,index) in playList"><span>{{item.amount}} ETH</span><span>{{item.time}}</span></div>
								</div>
							</div>
						</div>
						<div class="income" v-show="active == 1">
							<div v-show="!incomeRecord">
								<div class="canvasBox">									
									<div>
										<canvas id="c"></canvas>
										<span>{{incomeInfo.availableIncome.toFixed(4)}}</span>
									</div>
									<span> / ETH</span>
								</div>
								<div class="ul">
									<div class="list">
										<div>
											<img :src="require('@/assets/income/income1.png')" />
											<span></span>
										</div>
										<div><span>{{incomeInfo.staticIncome}}</span> ETH</div>
									</div>
									<div class="list">
										<div>
											<img :src="require('@/assets/income/income2.png')" />
											<span></span>
										</div>
										<div><span>{{incomeInfo.dynamicIncome}}</span> ETH</div>
									</div>
									<div class="list">
										<div>
											<img :src="require('@/assets/income/income3.png')" />
											<span></span>
										</div>
										<div><span>{{incomeInfo.totalIconme}}</span> ETH</div>
									</div>
									<div class="list" @click="incomeRecord = true">
										<div>
											<img :src="require('@/assets/income/income4.png')" />
											<span></span>
										</div>
										<div><span></span></div>
									</div>
								</div>
								<button class="publicButton" @click="withdrawProfix"></button>
							</div>
							<div v-show="incomeRecord">
								<div class="recordTitle" @click="incomeRecord = false">
									<span></span>
									<icon name="cross"></icon>
								</div>
								<div class="incomeRecord">
									<div v-for="(item,index) in incomeRecordList" :key="index"><span>{{item.type}}</span><span>{{item.time}}</span><span>+{{item.amount}} ETH</span></div>
									<!-- <div><span></span><span>2019-07-05   1600</span><span>+0.5500 ETH</span></div> -->
								</div>
							</div>
						</div>
						<div class="mine"  v-show="active == 2">
							<div v-show="mineShow ==1">
								<div class="userInfo">									
									<div>
										<span></span>
										<div 
										@click.stop
										v-clipboard:copy="mineJson.selfCode"
										v-clipboard:success="onCopySuccess"
										v-clipboard:error="onCopyError"><span>{{mineJson.selfCode}}</span><img :src="require('@/assets/copy.png')" /></div>
										<div
										@click.stop
										v-clipboard:copy="$root.address"
										v-clipboard:success="onCopySuccess"
										v-clipboard:error="onCopyError"><img :src="require('@/assets/wallet.png')" /><span>{{ `${$root.address.slice(0, 7)}...${$root.address.slice(-7)}` }}</span><img :src="require('@/assets/copy.png')" /></div>
									</div>
									<img :src="require(`@/assets/lv/lv${mineJson.lv}.png`)" @click="mineShow = 2" />
								</div>
								<div class="ul">
									<div class="list"
									@click.stop
									v-clipboard:copy="herf"
									v-clipboard:success="onCopySuccess"
									v-clipboard:error="onCopyError">
										<div>
											<img :src="require('@/assets/mine/mine1.png')" />
											<span></span>
										</div>
										<div><span></span></div>
									</div>
									<div class="list" @click="mineShow = 3">
										<div>
											<img :src="require('@/assets/mine/mine2.png')" />
											<span></span>
										</div>
										<div><span></span></div>
									</div>
									<div class="list">
										<div>
											<img :src="require('@/assets/mine/mine3.png')" />
											<span></span>
										</div>
										<span>{{see ? '******' : mineJson.otherCode}}</span>
										<div :class="{active:see}"><img :src="require('@/assets/see.png')" @click="see = !see" /></div>
									</div>
								</div>
							</div>
							<div v-show="mineShow ==2">
								<icon name="cross" @click="mineShow = 1"></icon>
								<div class="currentLv">
									<img :src="require(`@/assets/lv/lv${mineJson.lv}.png`)" />
									<span> V{{mineJson.lv}}</span>
									<p></p>
								</div>
								<div class="selectLv">
									<span></span>
									<div>
										<button v-for="(item,index) in lvValueList" :key="index" :class="{active:currentBuyLv == index}" @click="currentBuyLv = index" :disabled="mineJson.lv >= item.lv">V{{item.lv}}</button>
									</div>
									<div>
										<span></span>
										<span>{{lvValueList.length ? lvValueList[currentBuyLv].value : '0'}} ETH</span>
									</div>
								</div>
								<button class="publicButton" @click="buyLv"></button>
							</div>
							<div v-show="mineShow ==3">
								<div class="recordTitle" @click="mineShow = 1">
									<span></span>
									<icon name="cross"></icon>
								</div>
								<div class="inviteList">
									<div><span></span><span></span><span>/ETH</span><span>/</span></div>
									<div v-for="(item,index) in inviteList" :key="index">
										<span>{{item.code}}</span>
										<span><img :src="require(`@/assets/lv/lv${item.lv}.png`)" /></span>
										<span>{{item.totalAmount}}</span>
										<span>{{item.people}}</span>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="bottom">
			<statistics @MainPool="MainPool"/>
			<news @showLucky="showLucky"/>
			<dom-floot/>
		</div>
		
		<pop-ups 
		:show="popShow" 
		:type="popType"
		:popClick="popClick"
		:luckyAmount="luckyAmount"
		:lv="mineJson.newLevel"
		@bindCallback="getCode"
		@upgradeCallback="upgradeCallback">
		</pop-ups>
		
		<overlay :show="ruleOverlay" @click="ruleClick(1)">
			<div class="popUps" @click.stop>
				<div class="gameRule">
					<div class="gameRuleTitle"></div>
					<div class="gameRuleText">
						<pre v-text="require('@/common/rule')"></pre>
					</div>
					<div class="gameRuleBut">
						<span></span>
						<button class="publicButton" @click="ruleClick(2)"></button>
					</div>
				</div>
			</div>
		</overlay>
		
		<overlay :show="playSetMealOverlay" @click="playSetMealOverlay = false">
			<div class="playSetMeal"  @click.stop>
				<div>
					<icon name="cross" @click="playSetMealOverlay = false"></icon>
					<span></span>
					<div class="selectSetMeal">
						<div v-for="(item,index) in playInfo.setMealList" :key="index" :class="{active: playInfo.setMealActive === item.id}" @click="setMealClick(item.id,item.incomeProp,item.day)">{{item.day}} </div>
					</div>
					<div class="Expected">
						<span></span>
						<span> {{playInfo.expectedIncome}} ETH</span>
					</div>
					<button class="publicButton" @click="play"></button>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Button , Checkbox , Icon , Toast , Overlay } from 'vant'
	import publicTitle from '@/components/publicTitle.vue'
	import statistics from '@/components/statistics.vue'
	import news from '@/components/news.vue'
	import popUps from '@/components/pop-ups.vue'
	import Floot from '@/components/floot.vue'
	import '@/common/canvas.js'
	export default {
		name: 'app',
		components:{
			'van-button': Button,
			publicTitle,
			statistics,
			news,
			popUps,
			'dom-floot': Floot,
			Checkbox,
			Icon,
			Overlay
		},
		data(){
			return {
				popType: '', // 
				popShow: false, //
				//play
				playInitIf: false,
				playGameNav:[{title: ''},{title: ''},{title: ''}], //
				active: 0, //-
				fastPlay: [{value:1},{value:10},{value:30},{value:50}], //
				fastPlayActive: '', //-
				rule: localStorage.getItem('rule') == '' ? false : JSON.parse(localStorage.getItem('rule')), //
				ruleOverlay: false, //
				banlance: '0.00', //
				playAmount: '', //
				playSetMealOverlay: false, //
				playConfig: {
					maxLimit: '',//
					minLimit: '',//
					timeLimint: '',//
					bind: true,
				},
				playInfo: {
					currentAmount: '',//
					setMealList: [],//
					setMealActive: '',//
					expectedIncome: 0,//
					currentDay: '',//
					startTime: '',//
					endTime: '',//
					expired: false,//
				},
				playList: [],//
				playShow: '',
				//income
				incomeInitIf: false,
				incomeRecord: false, //
				incomeInfo: {
					totalIconme: '0.0000',
					staticIncome: '0.0000',
					dynamicIncome: '0.0000',
					availableIncome: '0.0000',
				},
				incomeRecordList: [],
				//mine
				mineInitIf: false,
				lvValueList:[], //
				currentBuyLv: 0, //
				mineJson: {
					selfCode: '',
					otherCode: '',
					lv: 0,
					newLevel: '',
				},
				mineShow: 1,
				inviteList: [],
				//
				luckyAmount: '',
				MainPoolNum: '',
				see: false,
				herf: '',
			}
		},
		methods:{
			async init(){
				window.canvas(230,230)
				
				// eth
				this.$web3.eth.getBalance(this.$root.address).then(result=>{
					this.banlance = result.fromWei().toFixed(2)
				})
				
				// ()
				this.$contract.get('Relations').methods.addressToShortCode(this.$root.address).call(async(err,result)=>{
					if(!err){
						let code = this.$web3.utils.hexToString(result);
						
						if(!Number(result)){
							this.mineJson.selfCode = '******'
							this.mineJson.otherCode = '******'
							this.showPop(true,4)
							this.playConfig.bind = false
						}else{
							this.herf = location.origin+'/#/?code='+code
							this.mineJson.selfCode = code
							let getIntroducer = await this.$contract.get('Relations').methods.getIntroducer(this.$root.address).call();
							this.mineJson.otherCode = this.$web3.utils.hexToString(await this.$contract.get('Relations').methods.addressToShortCode(getIntroducer).call());
						}
					}
				})
				
				// 
				this.$contract.get('Env').methods.getUint(0).call((err,result)=>{
					this.playConfig.timeLimint = result
				})
				
				this.playInit()
				
				// 
				for(let i = 1; i<=4; i++){
					this.$contract.get('Main').methods.planListMapping(i).call((err,result)=>{
						this.playInfo.setMealList.push({
							id: i,
							day: result.durationDay,
							incomeProp: result.incomeProp.fromWei('szabo')
						})
						this.playInfo.setMealList.sort(function(a,b){
							return a.day - b.day
						})
					})
				}
				
				// 
				this.$contract.get('Main').methods.currentProfixInfo(this.$root.address).call((err,result)=>{
					// 
					this.incomeInfo.staticIncome = result.st.fromWei().toFixed(4)
					// 
					this.incomeInfo.dynamicIncome = result.dy.fromWei().toFixed(4)
					// 
					this.incomeInfo.availableIncome = Number(this.incomeInfo.staticIncome) + Number(this.incomeInfo.dynamicIncome)
				})
				
				// 
				this.mineInit()
				
				// 
				this.$contract.get('Relations').methods.recommendList(this.$root.address).call((err,result)=>{
					result.forEach(async item=>{
						// 
						let code = this.$web3.utils.hexToString(await this.$contract.get('Relations').methods.addressToShortCode(item).call());
						// 
						let lv = await this.$contract.get('LevelNode').methods.levelOf(item).call();
						// 
						let totalAmount = (await this.$contract.get('Main').methods.addressInvestMapping(item).call()).totalAmount.fromWei().toFixed(2);
						// 
						let people = (await this.$contract.get('Relations').methods.recommendList(item).call()).length;
						
						this.inviteList.push({
							code: code,
							lv: lv,
							totalAmount: totalAmount,
							people: people
						})
					})
				})
				
				// 
				this.$contract.get('LevelNode').methods.levels().call((err,result)=>{
					let i = 1;
					result.prices.forEach(item=>{
						if(!Number(item)){
							return 
						}
						this.lvValueList.push({
							lv: i,
							value: item.fromWei()
						})
						i++
					})
				})
				
				// 
				this.$contract.get('Main').events.Event_EtherHistory({
					fromBlock: 0,
					filter: {ptype: '-1',owner:this.$root.address}
				}, (err,res)=>{
					this.playList.push({
						amount: res.returnValues.amount.fromWei().toFixed(2),
						time: this.toTime(res.returnValues.time,'Y-M-D'),
					})
				})
				
				// 
				this.$contract.get('Main').events.Event_EtherHistory({
					fromBlock: 0,
					filter: {ptype: [1,2,3],owner:this.$root.address}
				},(err,res)=>{
					let type;
					if(res.returnValues.ptype == '3'){
						type = ''
					}else if(res.returnValues.ptype == '1'){
						type = ''
					}else if(res.returnValues.ptype == '2'){
						type = ''
					}
					if(Number(res.returnValues.amount)){						
						this.incomeRecordList.push({
							type: type,
							time: this.toTime(res.returnValues.time,'Y-M-D'),
							amount: res.returnValues.amount.fromWei().toFixed(6)
						})
					}
				})
				
				// 
				this.playConfig.maxLimit = (await this.$contract.get('Env').methods.getUint(1).call()).fromWei()
				
				// 
				this.playConfig.minLimit = (await this.$contract.get('Env').methods.getUint(2).call()).fromWei()
			},
			navSwitch(index){
				this.active = index
				if(index == 1 && !this.incomeInitIf){
					this.incomeInitIf = true;
				}else if(index == 2 && !this.mineInitIf){
					this.mineInitIf = true;
				}
			},
			scrollId(id){
				document.getElementById(id).scrollIntoView({ block: 'start', behavior: 'smooth' })
			},
			showPop(show,type){
				this.popShow = show;
				this.popType = type;
			},
			popClick(rule){
				this.popShow = false;
			},
			ruleClick(type){
				if(type == 1){					
					localStorage.setItem('rule',false)
					this.rule = false;
					this.ruleOverlay = false;
				}else{
					localStorage.setItem('rule',true)
					this.rule = true;
					this.ruleOverlay = false;
				}
			},
			playInit(){
				// 
				this.$contract.get('Main').methods.addressInvestMapping(this.$root.address).call(async (err,result)=>{
					if(Number(result.createTime)){
						this.playShow = 2
						let planListMapping = await this.$contract.get('Main').methods.planListMapping(result.planID).call();
						this.playInfo.currentDay = planListMapping.durationDay
						this.playInfo.startTime = this.toTime(result.latestDespositTime,'Y-M-D H:i')
						this.playInfo.startTime1 = result.latestDespositTime
						this.playInfo.endTime = this.toTime(Number(result.latestDespositTime) + ((Number(planListMapping.durationDay) + 1) * 86400),'Y-M-D H:i')
						let endTime = Number(result.latestDespositTime) + ((Number(planListMapping.durationDay) + 1) * 86400);
						let currentTime = await this.currentTime();
						if(currentTime > endTime){
							// this.playInfo.expired = true
							this.$set(this.playInfo,'expired',true)
						}
					}else{
						this.playShow = 1
					}
					
					this.playInfo.currentAmount = result.totalAmount.fromWei()
					this.incomeInfo.totalIconme = result.totalWithdrawedProfix.fromWei().toFixed(4)
				})
			},
			async playDef(){
				let playAmount = Number(this.playAmount),
					maxLimit = Number(this.playConfig.maxLimit),
					minLimit = Number(this.playConfig.minLimit),
					bind = this.playConfig.bind,
					startTime = Number(this.playInfo.startTime1),
					timeLimint = Number(this.playConfig.timeLimint),
					income = Number(this.incomeInfo.availableIncome),
					// time = new Date().getTime() / 1000;
					time = (await this.$web3.eth.getBlock('latest').then()).timestamp;
				
				if(playAmount == '' || playAmount == 0){
					return Toast.fail('0')
				}else if(playAmount > maxLimit){
					return Toast.fail('')
				}else if(playAmount < minLimit){
					return Toast.fail('')
				}else if(!bind){
					return this.showPop(true,4)
				}else if(!JSON.parse(this.rule)){
					return this.ruleOverlay = true
				}else if(time < (startTime + timeLimint)){
					return Toast.fail('48')
				}else if(income > 0){
					return Toast.fail('')
				}
				
				this.playInfo.setMealActive = '';
				this.playInfo.expectedIncome = 0;
				this.playSetMealOverlay = true;
			},
			setMealClick(id,prop,day){
				this.playInfo.setMealActive = id;
				this.playInfo.expectedIncome = ((Number(this.playAmount) * Number(prop)) * Number(day)).toFixed(2);
			},
			async play(){
				if(this.playInfo.setMealActive == ''){
					return Toast.fail('')
				}else if(this.playInfo.setMealList[this.playInfo.setMealActive - 1].day < this.playInfo.currentDay){
					return Toast.fail('')
				}
				// let gas = (await this.$contract.get('Main').methods.deposit(this.playInfo.setMealActive).estimateGas({from : this.$root.address,value : this.playAmount.toWei()})) * 1.2;
				this.$contract.getI('Main').methods.deposit(this.playInfo.setMealActive).send({
					from : this.$root.address,
					value : this.playAmount.toWei(),
					// gas: Number(gas.toString().toFixed(0))
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err.toString())
				}).on('receipt' , result=>{
					this.playShow = 2
					this.playSetMealOverlay = false;
					this.playInit()
				})
			},
			async buyLv(){
				// if(this.mineJson.lv >= (this.currentBuyLv+1)){
				// 	return Toast.fail('')
				// }
				// let gas = (await this.$contract.get('Main').methods.paymentUpgradeLevel((this.currentBuyLv+1)).estimateGas({from : this.$root.address,value : this.lvValueList[this.currentBuyLv].value.toWei()})) * 1.2;
				this.$contract.getI('Main').methods.paymentUpgradeLevel((this.currentBuyLv+1)).send({
					from : this.$root.address,
					value : this.lvValueList[this.currentBuyLv].value.toWei(),
					// gas: Number(gas.toString().toFixed(0))
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err.toString())
				}).on('receipt' , result=>{
					this.mineShow = 1;
					this.mineJson.lv = this.currentBuyLv+1;
				})
			},
			async withdrawProfix(){
				if(!Number(this.incomeInfo.availableIncome)){
					return Toast.fail('0')
				}else if(this.incomeInfo.availableIncome > Number(this.MainPoolNum)){
					return Toast.fail('')
				}
				// let gas = (await this.$contract.get('Main').methods.withdrawProfixAll().estimateGas({from : this.$root.address})) * 1.2;
				this.$contract.getI('Main').methods.withdrawProfixAll().send({
					from : this.$root.address,
					// gas: Number(gas.toString().toFixed(0))
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err.toString())
				}).on('receipt' , result=>{
					this.incomeInfo.totalIconme = (Number(this.incomeInfo.totalIconme) + Number(this.incomeInfo.availableIncome)).toFixed(4);
					this.incomeInfo.availableIncome = 0;
					this.incomeInfo.staticIncome = 0;
					this.incomeInfo.dynamicIncome = 0;
					Toast.fail('')
				})
			},
			divestment(){
				this.$contract.get('Main').methods.divestment().call({from:this.$root.address},async (err,result)=>{
					if(err){
						return Toast.fail(err.toString())
					}
					// let gas = (await this.$contract.get('Main').methods.divestment().estimateGas({from : this.$root.address})) * 1.2;
					this.$contract.getI('Main').methods.divestment().send({
						from : this.$root.address,
						// gas: Number(gas.toString().toFixed(0))
					}).on('transactionHash' , txHash =>{
						console.log(txHash)
					}).on('error' , err=>{
						console.log(err.toString())
					}).on('receipt' , result=>{
						Toast.fail('')
						this.playShow = 1
						this.playInit()
					})
				})
			},
			getCode(){
				this.playConfig.bind = true;
				// 
				this.$contract.get('Relations').methods.addressToShortCode(this.$root.address).call(async(err,result)=>{
					let code = this.$web3.utils.hexToString(result);
					
					if(Number(result)){
						this.mineJson.selfCode = code
						let getIntroducer = await this.$contract.get('Relations').methods.getIntroducer(this.$root.address).call();
						this.mineJson.otherCode = this.$web3.utils.hexToString(await this.$contract.get('Relations').methods.addressToShortCode(getIntroducer).call());
					}
				})
			},
			showLucky(amount){
				this.luckyAmount = amount
				this.showPop(true,3)
			},
			upgradeCallback(){
				this.mineInit()
			},
			mineInit(){
				this.$contract.get('LevelNode').methods.verificationLevel(this.$root.address).call((err,result)=>{
					if(!err){
						this.mineJson.lv = result.currLevel
						this.mineJson.newLevel = result.newLevel
						if(result.newLevel > result.currLevel){
							this.showPop(true,2)
						}
					}
				})
			},
			MainPool(res){
				this.MainPoolNum = res
			},
			onCopySuccess(){
				Toast.fail('')
			},
			onCopyError(){
				Toast.fail('')
			}
		},
		mounted() {
			this.init()
			// window.addEventListener('scroll',(element)=>{
			// 	console.log(element.srcElement.scrollTop)
			// },true)
			// console.log(this.$contract.get('Achievement_LargeSmall').methods)
			
			// 
			// this.$contract.get('LevelNode').methods.achievementInfoMapping('0xEfadb7e311a70ce14e635e26b6066Bda8085F892').call((err,result)=>{
			// 	console.log(result)
			// 	console.log(this.BN(result.total).sub(this.BN(result.large)).toString().fromWei())
			// })
			
			this.$nextTick(() => {
				const box = document.getElementsByClassName('gameRuleText')[0]
				box.addEventListener('touchmove', e => e.stopPropagation(), false)
			})
		}
	}
</script>

