<template>
	<div>
		<overlay :show="showWatch" @click="popClick">
			<div class="diamond" v-if="type == 1 || type == 2 || type == 3"  @click.stop>
				<div class="maxLucky" v-if="type == 1">
					<p></p>
					<p></p>
					<span> 100 ETH</span>
					<button><span></span></button>
				</div>
				<div class="uplv" v-else-if="type == 2">
					<p></p>
					<p>M{{lvWatch}}</p>
					<span></span>
					<button @click="upgrade"><span></span></button>
				</div>
				<div class="minLucky" v-else-if="type == 3">
					<p></p>
					<p></p>
					<span> {{luckyAmountWatch}} ETH</span>
					<button @click="popClick"><span></span></button>
				</div>
			</div>
			<div class="register" v-else-if="type == 4" @click.stop>
				<div>
					<icon name="cross" @click="popClick"></icon>
					<p>Smart Player</p>
					<div class="bindCode">
						<span></span>
						<div class="bindCodeParent">
							<div v-for="(item,index) in bindCodeArr" :key="index" :class="{active:bindCode.length === index}">{{bindCode.substr(index,1)}}</div>
							<input type="text" v-model="bindCode" maxlength="6" oninput="value=value.replace(/[^\w\/]/ig,'')" />
						</div>
					</div>
					<div class="generateCode">
						<span></span>
						<div>
							<div v-for="(item,index) in generateCodeArr" :key="index" :class="{active:generateCode.length === index}">{{generateCode.substr(index,1)}}</div>
							<input type="text" v-model="generateCode" maxlength="6" oninput="value=value.replace(/[^\w\/]/ig,'')" />
						</div>
					</div>
					<button class="publicButton" @click="bind"></button>
				</div>
			</div>
			<div class="highest" v-else-if="type == 5">
				<div>
					<span></span>
					<div></div>
				</div>
			</div>
		</overlay>
	</div>
</template>

<script>
	import { Overlay , Icon , Toast } from 'vant';
	export default{
		components:{
			Overlay , Icon
		},
		props:{
			show:{
				type: Boolean,
				default: false
			},
			popClick:{
				type: Function
			},
			type:{},
			playAmount: {},
			luckyAmount: {},
			lv:{},
		},
		data(){
			return {
				showWatch: this.show,
				bindCode: '',
				bindCodeArr: [1,2,3,4,5,6],
				generateCode: '',
				generateCodeArr: [1,2,3,4,5,6],
				setMealList: [],
				setMealActive: '',
				expectedIncome: '',
				luckyAmountWatch: this.luckyAmount,
				lvWatch: this.lv,
			}
		},
		watch:{
			async show(){
				this.showWatch = this.show
			},
			type(){},
			luckyAmount(){
				this.luckyAmountWatch = this.luckyAmount
			},
			lv(){
				this.lvWatch = this.lv
			}
		},
		methods:{
			async bind(){ // 
				let reg = /^[\u4e00-\u9fa5_a-zA-Z0-9]+$/
				
				if (this.bindCode.length != 6) {
					return Toast.fail('')
				} else if (this.generateCode.length != 6) {
					return Toast.fail('')
				}
				
				let code = this.$web3.utils.stringToHex(this.generateCode.toUpperCase()), //
				    fromCode = this.$web3.utils.stringToHex(this.bindCode.toUpperCase()), //
				    thisAddress = await this.$contract.get('Relations').methods.shortCodeToAddress(code).call(),
				    fromAddress = await this.$contract.get('Relations').methods.shortCodeToAddress(fromCode).call()
				
				if (!parseInt(fromAddress, 16)) {
				    return Toast.fail('')
				} else if (parseInt(thisAddress, 16)) {
				    return Toast.fail('')
				}
				
				this.$contract.get('Relations').methods.addRelationEx(fromAddress, code, '0x00')
				.call({ from: this.$root.address }, async (err, res) => {
					if (!err) {
						if (res === '0') {
							// let gas = (await this.$contract.get('Relations').methods.addRelationEx(fromAddress, code, '0x00').estimateGas()) * 1.5;
							this.$contract.getI('Relations').methods.addRelationEx(fromAddress, code, '0x00').send({
								from : this.$root.address,
								// gas: Number(gas.toString().toFixed(0))
							}).on('transactionHash' , txHash =>{
								console.log(txHash)
							}).on('error' , err=>{
								console.log(err.toString())
							}).on('receipt' , result=>{
								this.$emit('bindCallback','true')
								this.popClick()
								Toast.success('')
							})
						} else {
							Toast.fail('')
						}
					}
				})
			},
			async upgrade(){
				// let gas = (await this.$contract.get('LevelNode').methods.upgrade().estimateGas({from: this.$root.address})) * 1.2;
				this.$contract.getI('LevelNode').methods.upgrade().send({
					from : this.$root.address,
					// gas: Number(gas.toString().toFixed(0))
				}).on('transactionHash' , txHash =>{
					console.log(txHash)
				}).on('error' , err=>{
					console.log(err.toString())
				}).on('receipt' , result=>{
					this.$emit('upgradeCallback','true')
					this.popClick()
					Toast.success('')
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.van-overlay{
		display: flex;
		align-items: center;
		justify-content: center;
	}
	.diamond{
		width: 100%;
		height: 370px;
		display: flex;
		justify-content: center;
		align-items: flex-end;
		
		&>div{			
			width: 472px;
			height: 538px;
			padding: 280px 66px 0 86px;
			background-image: url(../assets/diamond.png);
			background-size: 100% 100%;
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;

			&>p{
				background: linear-gradient(180deg,#f8bf51,#f99724);
				-webkit-background-clip: text;
				color: transparent;
				font-size: 34px;
				font-weight: bold;
			}
			
			&>p:nth-child(2){
				background: linear-gradient(180deg,#f89451,#f96f24);
				-webkit-background-clip: text;
				color: transparent;
			}
			
			&>span{
				font-size: 32px;
				margin: 21px 0 0;
			}
			
			&>button{
				width: 215px;
				height: 60px;
				position: absolute;
				left: 138px;
				bottom: -30px;
				font-size: 32px;
				background-color: #fff;
				border: none;
				border-radius: 50px;
				
				&>span{					
					background: linear-gradient(to right,#f8bf51,#f99724);
					-webkit-background-clip: text;
					color: transparent;
				}
			}
		}
	}
	.register{
		width: 100%;
		padding: 0 20px;
		
		&>div{
			width: 100%;
			height: 666px;
			border-radius: 20px;
			border: 2px solid #DDA33F;
			position: relative;
			padding: 34px 0 0;
			background-color: #121212;
			display: flex;
			flex-direction: column;
			align-items: center;
			
			&>i{
				position: absolute;
				right: 40px;
				top: 30px;
			}
			
			&>p{
				font-size: 40px;
				color: #DDA33F;
				margin-bottom: 50px;
			}
			
			&>div{
				width: 510px;
				display: flex;
				flex-direction: column;
				align-items: center;
				margin-bottom: 60px;
				
				&>div{
					width: 100%;
					display: flex;
					justify-content: space-between;
					position: relative;
					
					&>div{
						width:60px;
						height:60px;
						border-radius:8px;
						background-color: transparent;
						color: #fff;
						text-align: center;
						border:2px solid #666;
						display: flex;
						justify-content: center;
						align-items: center;
						font-size: 28px;
					}
					
					&>input{
						position: absolute;
						width: 100%;
						height: 100%;
						left: 0;
						top: 0;
						opacity: 0;
						color: #000;
					}
					
					.active{
						border-color:#DDA33F;
					}
				}
				
				&>span{
					font-size: 32px;
					margin-bottom: 38px;
				}
			}
		}
	}
	.highest{
		&>div{
			width: 450px;
			height: 506px;
			background-image: url(../assets/highest.png);
			background-size: 100% 100%;
			padding: 385px 48px 0 60px;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
		&>div>span{
			font-size: 28px;
		}
		&>div>div{
			font-size: 24px;
			border: 1px solid #fff;
			border-radius: 50px;
			padding: 10px 25px;
			line-height: 1;
			margin-top: 20px;
		}
	}
	
	
</style>
